___CSS_LOADER_EXPORT___.push([module.id, `* {
  color: white;
  font-family: "Courier New", Courier, monospace;
}

html,
body {
  display: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: grey;
}

/*For mobile*/

#main-content {
  min-height: 100vh;
  display: grid;
  grid-template: auto 5fr auto / 1fr;
}

.nav-bar {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 1em;
  background-color: black;
}

.dropdown-menu {
  display: inline-block;
  position: relative;
}

.dropdown-header {
  padding: 0.5em;
  font-size: large;
  font-weight: 700;
}

.dropdown-header:hover {
  cursor: pointer;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: white;
  z-index: 999;
}

.dropdown-content .dropdown-item {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content .dropdown-item:hover {
  background-color: aliceblue;
  transition: all 0.1s ease;
  cursor: pointer;
}

.dropdown-visible {
  display: block;
}
.normal-header,
.normal-menu {
  display: none;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 1em;
}

.image-slide-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1em;

  .image-carousel {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    overflow: hidden;
    width: 500px;
    gap: 1em;

    .image-slide {
      display: block;
      opacity: 0.5;
      flex: 0 0 auto;

      .image-item {
        border-radius: 1em;
        height: 500px;
        width: 500px;
      }
    }

    .image-visible {
      display: block;
      opacity: 1;
      transition: opacity 0.2s ease;
    }
  }

  .image-navigation-bar {
    display: flex;
    gap: 1em;

    .nav-circle {
      height: 16px;
      width: 16px;
      border-radius: 8px;
      border-color: transparent;
      background-color: #334155;
    }

    .selected {
      background-color: #06070a;
    }
  }

  .image-buttons {
    display: flex;
    gap: 1em;
    .image-carousel-button {
      align-self: center;
      padding: 0.5em 1em;
      width: 150px;
      border-radius: 1em;
      border-color: transparent;
      background-color: black;
    }
  }
}

.footer {
  background-color: black;
  padding: 1em;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

@media only screen and (min-width: 768px) {
  /* For desktop: */
  .dropdown-menu {
    display: none;
  }

  .normal-header {
    display: block;
    padding: 0.5em;
    font-size: large;
    font-weight: 700;
  }

  .normal-menu {
    display: flex;
    gap: 1em;
    margin-left: auto;
    padding: 0.5em;
  }

  .normal-header:hover,
  .normal-menu:hover {
    cursor: pointer;
  }

  .footer {
    justify-content: center;
  }
}
